<template>
  <!-- Continer do grfico de barras -->
  <div>
    <!-- Exibe o ttulo do grfico, que  passado como prop para o componente -->
    <h3>{{ title }}</h3>
    <!-- Elemento canvas onde o grfico de barras ser renderizado -->
    <canvas ref="chartCanvas" style="width: 700px; max-height: 250px;"></canvas>
  </div>
</template>

<script>
// Importando funes necessrias do Vue e a biblioteca Chart.js
import { defineComponent, onMounted, ref } from 'vue';
import { Chart as ChartJS, BarElement, CategoryScale, LinearScale, Title, Tooltip, Legend } from 'chart.js';

// Registrar os controladores necessrios para a criao de grficos de barras no Chart.js
ChartJS.register(
  BarElement,       // Define o tipo de grfico de barras
  CategoryScale,    // Escala para as categorias (eixo X)
  LinearScale,      // Escala linear para os valores (eixo Y)
  Title,            // Adiciona ttulo ao grfico
  Tooltip,          // Adiciona tooltip (informaes ao passar o mouse)
  Legend            // Exibe legenda do grfico
);

export default defineComponent({
  // Definindo as propriedades (props) que o componente recebe
  props: {
    data: Object,   // Dados do grfico (deve ser um objeto com as informaes)
    title: String,  // Ttulo do grfico
  },
  setup(props) {
    // Referncia para o elemento canvas onde o grfico ser desenhado
    const chartCanvas = ref(null);

    // Mtodo que  executado assim que o componente  montado na tela
    onMounted(() => {
      // Inicializa o grfico de barras usando o Chart.js e passa a referncia do canvas
      new ChartJS(chartCanvas.value, {
        type: 'bar',  // Define o tipo do grfico como 'bar' (barra)
        data: props.data,  // Passa os dados para o grfico
        options: {
          responsive: true,              // Faz com que o grfico se ajuste automaticamente ao tamanho da tela
          maintainAspectRatio: true,     // Garante que o grfico mantenha a proporo
          aspectRatio: 1,                // Define uma proporo fixa de 1:1 para o grfico
          plugins: {
            legend: {
              display: false,  // Oculta a legenda do grfico
            },
          },
        },
      });
    });

    // Retorna a referncia do canvas para ser utilizada no template
    return {
      chartCanvas,
    };
  },
});
</script>
