<template>
  <!-- Estrutura principal da pgina de grficos -->
  <div class="charts-page">
    <!-- Navbar  um componente reutilizvel, usado para navegao -->
    <Navbar />

    <!-- Grid onde os grficos e a caixa de relatrio so exibidos -->
    <div class="charts-grid">
      <!-- Caixa do grfico dos livros mais bem avaliados -->
      <div v-if="topRatedBooksData" class="chart-box">
        <h3 class="chart-title">Top 5 Rated Books</h3>
        <!-- Componente de grfico de barras que recebe os dados via prop -->
        <BarChart :data="topRatedBooksData" />
      </div>
      <!-- Exibe uma mensagem enquanto os dados ainda esto sendo carregados -->
      <div v-else>Loading Top Rated Books...</div>

      <!-- Caixa do grfico dos gneros com mais livros -->
      <div v-if="genresData" class="chart-box">
        <h3 class="chart-title">Genres with Most Books</h3>
        <div v-if="genresData" class="chart-box-genre">
          <!-- Componente de grfico de pizza que recebe os dados via prop -->
          <PieChart :data="genresData" />
        </div>
      </div>
      <!-- Exibe uma mensagem enquanto os dados ainda esto sendo carregados -->
      <div v-else>Loading Genres...</div>

      <!-- Caixa do grfico dos autores com mais livros -->
      <div v-if="authorsData" class="chart-box">
        <h3 class="chart-title">Top 5 Authors with Most Books</h3>
        <!-- Componente de grfico de barras que recebe os dados via prop -->
        <BarChart :data="authorsData" />
      </div>
      <!-- Exibe uma mensagem enquanto os dados ainda esto sendo carregados -->
      <div v-else>Loading Authors...</div>

      <!-- Caixa de gerao de relatrios -->
      <div class="report-box">
        <h3 class="report-title">Generate Report</h3>
        <div class="report-content">
          <!-- Imagem ilustrativa do relatrio -->
          <img src="../assets/report.png" alt="Report Image" class="report-image" />
          <div class="report-text-button">
            <!-- Texto explicativo sobre a funcionalidade do relatrio -->
            <p>
              This section allows you to generate a comprehensive report, which includes all the charts displayed on the page. 
              You can download a PDF containing the visualizations of top-rated books, most popular genres, and the authors with the largest number of books. 
              This tool will help you keep track of the most important data and is an essential feature for analyzing book statistics efficiently.
              Feel free to explore the charts before generating your report, and make sure to have all the necessary data to make your report complete.
            </p>
            <!-- Boto para gerar o PDF -->
            <button class="generate-pdf" @click="generatePDF">Generate PDF</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue'; // Importao de hooks do Vue para reatividade e ciclo de vida
import Navbar from '../components/Navbar.vue'; // Navbar importada como componente
import BarChart from '../components/BarChart.vue'; // Componente de grfico de barras
import PieChart from '../components/PieChart.vue'; // Componente de grfico de pizza
import jsPDF from 'jspdf'; // Biblioteca para gerao de PDFs
import html2canvas from 'html2canvas'; // Biblioteca para capturar a pgina como imagem
import api from '../services/api'; // Servio de API para buscar dados do backend

export default {
  components: {
    BarChart, // Registro do componente BarChart
    PieChart, // Registro do componente PieChart
    Navbar, // Registro do componente Navbar
  },
  setup() {
    // Referncias reativas para armazenar os dados dos grficos
    const topRatedBooksData = ref(null);
    const genresData = ref(null);
    const authorsData = ref(null);

    // Funo para buscar os dados dos grficos na API
    const fetchChartData = async () => {
      try {
        const { data } = await api.getChartData(); // Chama o endpoint de API
        if (data) {
          // Prepara os dados para o grfico de livros mais bem avaliados
          if (data.topRatedBooks && Array.isArray(data.topRatedBooks)) {
            const colors = [
              'rgba(255, 99, 132, 0.6)', // Lista de cores para os grficos
              'rgba(54, 162, 235, 0.6)',
              'rgba(255, 206, 86, 0.6)',
              'rgba(75, 192, 192, 0.6)',
              'rgba(153, 102, 255, 0.6)',
            ];

            topRatedBooksData.value = {
              labels: data.topRatedBooks.map((book) => book.title), // Rtulos baseados nos ttulos dos livros
              datasets: [
                {
                  label: "Reviews", // Rtulo do dataset
                  data: data.topRatedBooks.map((book) => book.reviews), // Dados das avaliaes
                  backgroundColor: colors.slice(0, data.topRatedBooks.length), // Aplica cores
                },
              ],
            };
          }

          // Prepara os dados para o grfico de gneros literrios
          if (data.genres && Array.isArray(data.genres)) {
            const colors = [
              'rgba(255, 99, 132, 0.6)',
              'rgba(54, 162, 235, 0.6)',
              'rgba(255, 206, 86, 0.6)',
              'rgba(75, 192, 192, 0.6)',
              'rgba(153, 102, 255, 0.6)',
              'rgba(255, 159, 64, 0.6)',
            ];

            genresData.value = {
              labels: data.genres.map((genre) => genre.name), // Rtulos baseados nos nomes dos gneros
              datasets: [
                {
                  label: "Books", // Rtulo do dataset
                  data: data.genres.map((genre) => genre.count), // Quantidade de livros por gnero
                  backgroundColor: colors.slice(0, data.genres.length), // Aplica cores
                },
              ],
            };
          }

          // Prepara os dados para o grfico de autores mais prolficos
          if (data.topAuthors && Array.isArray(data.topAuthors)) {
            const colors = [
              'rgba(75, 192, 192, 0.6)',
              'rgba(255, 206, 86, 0.6)',
              'rgba(153, 102, 255, 0.6)',
              'rgba(54, 162, 235, 0.6)',
              'rgba(255, 99, 132, 0.6)',
            ];

            authorsData.value = {
              labels: data.topAuthors.map((author) => author.name), // Rtulos baseados nos nomes dos autores
              datasets: [
                {
                  label: "Books", // Rtulo do dataset
                  data: data.topAuthors.map((author) => author.count), // Quantidade de livros por autor
                  backgroundColor: colors.slice(0, data.topAuthors.length), // Aplica cores
                },
              ],
            };
          }
        }
      } catch (error) {
        console.error("Error fetching chart data:", error); // Loga erros no console
      }
    };

    // Funo para gerar o PDF dos grficos
    const generatePDF = async () => {
      try {
        const pdf = new jsPDF(); // Cria uma instncia do jsPDF
        const chartContainer = document.querySelector('.charts-grid'); // Seleciona a rea dos grficos

        const canvas = await html2canvas(chartContainer); // Captura os grficos como imagem
        const imageData = canvas.toDataURL('image/png'); // Converte para formato PNG

        pdf.addImage(imageData, 'PNG', 10, 10, 190, 240); // Adiciona a imagem no PDF
        pdf.save('charts-report.pdf'); // Salva o PDF com o nome especificado
      } catch (error) {
        console.error("Error generating PDF:", error); // Loga erros no console
      }
    };

    onMounted(fetchChartData); // Executa a busca dos dados ao montar o componente

    return {
      topRatedBooksData, // Dados reativos para o grfico de livros mais bem avaliados
      genresData, // Dados reativos para o grfico de gneros
      authorsData, // Dados reativos para o grfico de autores
      generatePDF, // Funo para gerar PDF
    };
  },
};
</script>

<style scoped>
