  font-family: 'Arial', sans-serif;
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.content {
  margin-top: 70px;
  padding: 20px;
}

.grid-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 20px;
}

.box {
  background: #f4f4f4;
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.report-box {
  background: white;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  text-align: center;
  height: auto;
}

.report-title {
  font-size: 1.2em;
  margin-bottom: 15px;
  font-weight: 600;
  color: #333;
}

.report-content {
  display: flex;
  flex-direction: row;
  /* Coloca a imagem e o contedo (texto + boto) lado a lado */
  align-items: flex-start;
  /* Alinha o contedo no topo, ao lado da imagem */
}

.report-image {
  max-width: 24%;
  /* Define o tamanho da imagem */
  height: auto;
  margin-right: 20px;
  /* Espaamento entre a imagem e o contedo */
}

.report-text-button {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  
}

.report-text-button p {
  text-align: justify; /* Justifica o texto */
  line-height: 1.6; /* Aumenta a altura da linha para melhor legibilidade */
}

.pdf-button {
  padding: 10px 20px;
  background-color: #651f71;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
}

.pdf-button:hover {
  background-color: #530f5a;
}

.charts-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  /* Mantm 2 colunas */
  gap: 80px;
  /* Define o espao entre as boxes */
  margin-top: 120px;
}


.chart-box-genre {
  /* Centraliza verticalmente */
  flex-direction: row;
  /* Coloca legenda e grfico lado a lado */
}

.chart-box {
  width: 100%;
  height: 300px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  /* Coloca o ttulo no topo */
  align-items: center;
  padding: 20px;
}


.chart-boxGenre {
  width: 100%;
  height: 300px;
