  /* Coloca o ttulo no topo */
  align-items: center;
  padding: 20px;
}

.chart-title {
  font-size: 1.1em;
  /* Tamanho menor para os ttulos */
  margin-bottom: 10px;
  /* Espao entre o ttulo e o grfico */
  color: #333;
  font-weight: 600;
  text-align: center;
  /* Centraliza o ttulo */
}

.chart-box,
.report-box {
  background: white;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  padding: 20px;
  /* Adiciona espaamento interno */
}

.generate-pdf {
  padding: 10px 20px;
  background-color: #651f71;
