<template>
  <div class="card">
    <!-- Exibe a imagem do livro se a propriedade 'image' do objeto 'book' for fornecida -->
    <div class="img">
      <!-- Usa o baseURL com a imagem do livro e renderiza a imagem se a propriedade 'image' existir -->
      <img 
        :src="book.image ? `${baseURL}${book.image}` : ''"  
        alt="Capa do livro" 
        v-if="book.image" 
      />
    </div>

    <!-- Exibe as informaes do livro (ttulo, autor e ano) -->
    <div class="text">
      <p class="h3">{{ book.title }}</p> <!-- Exibe o ttulo do livro -->
      <p class="p">{{ book.author }} ({{ book.year }})</p> <!-- Exibe o autor e o ano do livro -->
    
      <!-- Boto para reservar o livro -->
      <div class="icon-box">
        <p class="reserve-button" @click="reserveBook">
          Borrow/Reserve
        </p>
      </div>
    </div>
  </div>
</template>

<script>
// Script do componente
export default {
  props: {
    book: {
      type: Object,   // Espera um objeto 'book' como prop
      required: true, // A prop 'book'  obrigatria
    },
  },
  data() {
    return {
      baseURL: 'http://localhost:3000', // URL base do backend para buscar as imagens
    };
  },
  methods: {
    // Mtodo chamado quando o boto "Borrow/Reserve"  clicado
    reserveBook() {
      this.$emit('reserve', this.book);  // Emite um evento 'reserve' passando o livro como argumento
    },
  },
};
</script>

<style scoped>
/* Estilo do componente 'card' */
.card {
  width: 280px;  /* Define a largura do card */
  background: white;  /* Fundo branco */
  border-radius: 10px;  /* Bordas arredondadas */
  box-shadow: 0px 0px 14px -2px #bebebe;  /* Sombra suave ao redor do card */
  transition: transform 0.3s ease, box-shadow 0.3s ease, background-color 0.3s ease; /* Transies para suavizar animaes */
}

.card:hover {
  cursor: pointer;  /* Exibe o cursor como ponteiro quando o mouse est sobre o card */
  transform: translateY(-5px);  /* Eleva o card quando o mouse est sobre ele */
  box-shadow: 0px 4px 20px -2px rgba(0, 0, 0, 0.2);  /* Intensifica a sombra */
  background-color: #f9f9f9;  /* Muda a cor do fundo para um tom mais claro */
}

/* Estilo da imagem do livro */
.img {
  width: 100%;  /* Largura total */
  height: 200px;  /* Altura definida para a imagem */
  border-top-left-radius: 10px;  /* Bordas arredondadas no canto superior esquerdo */
  border-top-right-radius: 10px;  /* Bordas arredondadas no canto superior direito */
  background: linear-gradient(#7980c5, #9198e5);  /* Gradiente de fundo para a rea da imagem */
  display: flex;
  align-items: center;  /* Alinha a imagem verticalmente no centro */
  justify-content: center;  /* Alinha a imagem horizontalmente no centro */
}

/* Estilo para a imagem dentro da rea .img */
.img img {
  max-width: 90%;  /* Largura mxima de 90% da rea disponvel */
  max-height: 90%;  /* Altura mxima de 90% da rea disponvel */
  object-fit: contain;  /* Garante que a imagem no ser cortada */
  border-radius: 5px;  /* Bordas arredondadas na imagem */
}

/* Estilo da seo de texto do livro (ttulo, autor, etc.) */
.text {
  padding: 7px 20px;  /* Espaamento interno do continer */
  display: flex;
  flex-direction: column;  /* Exibe os itens em coluna */
  align-items: space-around;  /* Espaamento entre os itens */
}

/* Estilo do ttulo do livro */
.text .h3 {
  font-family: system-ui;
  font-size: medium;
  font-weight: 600;
  color: black;  /* Cor preta para o ttulo */
  text-align: center;  /* Alinha o ttulo ao centro */
}

/* Estilo do pargrafo com o autor e ano do livro */
.text .p {
  font-family: system-ui;
  color: #999999;  /* Cor cinza claro para o texto */
  font-size: 13px;
  margin: 0px;  /* Remove margem */
  text-align: center;  /* Alinha o texto ao centro */
  padding: 5px;  /* Adiciona um pequeno padding para espaamento */
}

/* Estilo do continer para o boto de reserva */
.icon-box {
  font-family: "Inika", serif;
  color: #fff;  /* Cor do texto branco */
  margin: 10px;
  padding: 4px;
  background-color: #7980c5;  /* Cor de fundo para o boto */
  border-radius: 10px;  /* Bordas arredondadas */
  text-align: center;  /* Alinha o texto ao centro */
  font-size: small;  /* Tamanho da fonte pequeno */
}

/* Estilo para o span dentro do .icon-box (se houver) */
.icon-box .span {
  font-family: "Inika", serif;
  font-size: small;
  font-weight: 500;
  color: #fff;
}
</style>