<template>
    <!-- Continer principal das notificaes -->
    <div class="notifications-container">
        <!-- Lista de notificaes, exibida apenas se houver reservas -->
        <ul v-if="reservations.length > 0">
            <!-- Itera sobre as reservas para exibir cada item -->
            <li v-for="reservation in reservations" :key="reservation._id">
                <div class="reservation-item">
                    <!-- Exibe informaes da reserva: ttulo do livro e prazo para devoluo -->
                    <p>
                        <strong>Book:</strong> {{ getBookTitle(reservation.bookId) }} <br />
                        <strong>Deadline:</strong> You have {{ daysLeft(reservation.reservedAt) }} days to return
                        ({{ returnDate(reservation.reservedAt) }})
                    </p>
                    <div class="divider"></div>
                </div>
            </li>
        </ul>
        <!-- Mensagem padro caso no haja reservas -->
        <p v-else>No active reservations.</p>
    </div>
</template>

<script>
// Importa as APIs usadas para buscar informaes de autenticao e livros
import apiAuth from "../axios";
import apiLibrary from "../services/api";

export default {
    data() {
        return {
            reservations: [], // Lista de reservas do usurio logado
            bookTitles: {}, // Cache de ttulos dos livros para evitar mltiplas buscas
            showNotifications: false, // Controla a exibio do componente de notificaes
        };
    },
    methods: {
        // Busca as reservas do usurio logado
        async fetchReservations() {
            console.log("Iniciando fetchReservations...");
            try {
                // Obtm o ID do usurio armazenado no localStorage
                const userId = localStorage.getItem("userId");
                if (!userId) {
                    console.error("Usurio no encontrado no localStorage.");
                    return;
                }
                // Faz uma requisio  API para buscar as reservas do usurio
                const response = await apiAuth.get(`/auth/users/${userId}`);
                console.log("Resposta da API de reservas:", response.data);
                this.reservations = response.data.reservations || [];
                console.log("Reservas carregadas:", this.reservations);

                // Obtm os IDs dos livros relacionados s reservas
                const bookIds = this.reservations.map((reservation) => reservation.bookId);
                // Busca os ttulos dos livros correspondentes
                await this.fetchBookTitles(bookIds);
            } catch (error) {
                console.error("Erro ao buscar reservas:", error);
            }
        },
        // Busca os ttulos dos livros com base nos IDs fornecidos
        async fetchBookTitles(bookIds) {
            console.log("Buscando ttulos para os IDs:", bookIds);
            try {
                // Faz uma requisio  API para buscar informaes dos livros
                const response = await apiLibrary.getBooks();
                console.log("Livros recebidos:", response.data);
                const books = response.data;

                // Mapeia os IDs dos livros para seus ttulos
                this.bookTitles = bookIds.reduce((acc, id) => {
                    const book = books.find((b) => b._id === id);
                    if (book) {
                        acc[id] = book.title;
                    } else {
                        console.warn(`Livro com ID ${id} no encontrado.`);
                    }
                    return acc;
                }, {});

                console.log("Mapeamento de ttulos finalizado:", this.bookTitles);
            } catch (error) {
                console.error("Erro ao buscar ttulos dos livros:", error);
            }
        },
        // Retorna o ttulo de um livro com base no ID, ou uma mensagem padro se no encontrado
        getBookTitle(bookId) {
            console.log("Obtendo ttulo para o livro com ID:", bookId);
            return this.bookTitles[bookId] || "Ttulo no encontrado";
        },
        // Calcula os dias restantes para devoluo de um livro reservado
        daysLeft(reservedAt) {
            const reservedDate = new Date(reservedAt); // Data da reserva
            const dueDate = new Date(reservedDate); // Data limite para devoluo
            dueDate.setDate(dueDate.getDate() + 14); // Adiciona 14 dias ao prazo

            const now = new Date(); // Data atual
            const timeDiff = dueDate - now; // Diferena em milissegundos
            return Math.ceil(timeDiff / (1000 * 60 * 60 * 24)); // Converte para dias
        },
        // Retorna a data de vencimento formatada para exibio
        returnDate(reservedAt) {
            const reservedDate = new Date(reservedAt); // Data da reserva
            reservedDate.setDate(reservedDate.getDate() + 14); // Adiciona 14 dias ao prazo
            return reservedDate.toLocaleDateString(); // Formata a data no padro local
        },
        // Alterna a exibio do componente de notificaes
        toggleNotifications() {
            this.showNotifications = !this.showNotifications;
            console.log("Notificaes visveis?", this.showNotifications);
        },
    },
    mounted() {
        // Chamado quando o componente  montado, inicializa as reservas
        console.log("Componente Notifications montado.");
        this.fetchReservations();
    },
    watch: {
        // Observa mudanas na varivel `showNotifications` para atualizar as reservas
        showNotifications(newValue) {
            if (newValue) {
                console.log("Componente Notifications ativado.");
                this.fetchReservations();
            }
        },
    },
};
</script>

<style>
/* Importa a fonte Nunito */
@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@400;600&display=swap');

/* Estilo do continer de notificaes */
.notifications-container {
    font-family: 'Nunito', serif;
    position: absolute;
    top: 50px;
    right: 20px;
    border: 1px solid #ccc;
    border-radius: 8px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
    padding: 10px;
    width: 300px;
    background-color: rgba(255, 255, 255, 0.9);
    border: 4px solid rgb(0, 0, 0); /* Apenas para teste */
    z-index: 1100; /* Para garantir que esteja acima de outros elementos */
}

/* Remove estilos padro da lista */
.notifications-container ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

/* Linha separadora entre notificaes */
.divider {
    border-top: 4px solid #000000;
    border-radius: 20px;
}
</style>
