<template>
  <!-- O template define a estrutura HTML do componente -->
  <div>
    <!-- Exibe o ttulo do grfico, que  passado como prop -->
    <h3>{{ title }}</h3>
    
    <!-- Continer para o grfico, que  um canvas onde o grfico ser renderizado -->
    <div class="chart-container">
      <!-- Referncia ao canvas onde o grfico ser desenhado -->
      <canvas ref="chartCanvas" style="width: 700px; max-height: 250px;"></canvas>
    </div>
  </div>
</template>

<script>
import { defineComponent, onMounted, ref } from 'vue';
// Importa as funes do Vue para a definio do componente e manipulao do ciclo de vida
import { Chart as ChartJS, ArcElement, Title, Tooltip, Legend } from 'chart.js';
// Importa a biblioteca Chart.js e os elementos necessrios para criar um grfico do tipo 'pie'

ChartJS.register(ArcElement, Title, Tooltip, Legend);
// Registra os elementos necessrios para o grfico 'pie' (grfico de pizza) no Chart.js

export default defineComponent({
  // Define o componente Vue, usando a funo 'defineComponent'
  props: {
    data: Object,  // A prop 'data' ser um objeto com os dados que sero usados para criar o grfico
    title: String, // A prop 'title' ser uma string que define o ttulo do grfico
  },
  setup(props) {
    // Funo 'setup'  usada para inicializar o estado do componente em Vue 3
    const chartCanvas = ref(null); // Cria uma referncia ao canvas onde o grfico ser desenhado

    onMounted(() => {
      // 'onMounted'  um hook do ciclo de vida do Vue que  executado aps o componente ser montado no DOM
      new ChartJS(chartCanvas.value, {
        type: 'pie', // Define que o grfico ser do tipo 'pie' (pizza)
        data: props.data, // Passa os dados do grfico para o grfico do Chart.js
        options: {
          responsive: true, // Faz o grfico ser responsivo, ajustando-se automaticamente ao tamanho da tela
          maintainAspectRatio: false, // Permite o controle do layout do grfico, alterando sua proporo
          aspectRatio: 1.5, // Define a proporo de largura para altura do grfico
          layout: {
            padding: {
              right: -225, // Ajusta o espao  direita do grfico, necessrio para a legenda
            },
          },
          plugins: {
            legend: {
              position: 'left', // A posio da legenda   esquerda
              align: 'start', // A legenda ser alinhada ao topo
              labels: {
                boxWidth: 20, // Define a largura das caixas de legenda
                padding: 15, // Espaamento entre os itens da legenda
              },
            },
          },
        },
      });
    });

    return {
      chartCanvas, // Retorna a referncia ao canvas para que o Chart.js possa desenh-lo
    };
  },
});
</script>

<style scoped>
/* Estilos para o componente */
.chart-container {
  display: flex; /* Exibe o contedo do continer como um flexbox */
  flex-direction: row; /* Organiza os itens em linha (horizontais) */
  justify-content: space-between; /* Espaa o grfico e a legenda de maneira equilibrada */
  align-items: center; /* Alinha os itens verticalmente no centro */
}

.chart-container canvas {
  /* No h mais necessidade de margens para empurrar o grfico, mas o padding no layout do grfico ajuda */
}
</style>